
   
  import { defineComponent, ref } from 'vue';

  export default defineComponent({
    name: 'RatingComponent',
    emits: ["showComponent"],
    setup(props, { emit }) {

      let activeBtn = ref('')

      const handleClick = () => {
        emit("showComponent", [false, activeBtn.value])
      }
      return {activeBtn, handleClick}
    }
  });


